@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Black.eot");
  src: url("../fonts/Roboto-Black.eot?#iefix") format("embedded-opentype"),url("../fonts/Roboto-Black.woff2") format("woff2"),url("../fonts/Roboto-Black.woff") format("woff");
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Light.eot");
  src: url("../fonts/Roboto-Light.eot?#iefix") format("embedded-opentype"),url("../fonts/Roboto-Light.woff2") format("woff2"),url("../fonts/Roboto-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Medium.eot");
  src: url("../fonts/Roboto-Medium.eot?#iefix") format("embedded-opentype"),url("../fonts/Roboto-Medium.woff2") format("woff2"),url("../fonts/Roboto-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular.eot");
  src: url("../fonts/Roboto-Regular.eot?#iefix") format("embedded-opentype"),url("../fonts/Roboto-Regular.woff2") format("woff2"),url("../fonts/Roboto-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

:root {
  --text: #323232;
  --accent-color: #c99c47;
}

html {
  box-sizing: border-box;
  font-size: 14px;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html,
body {
  height: 100%;
  position: relative;
}

body {
  font-family: "Roboto";
  font-size: 14px;
  font-weight: 400;
  color: var(--text);
  line-height: 1;
}

ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

button:focus,
input:focus {
  outline: transparent;
}

button {
  padding: 0;
  margin: 0;
  border: 0;
  background-color: transparent;
  border-radius: 0;
  cursor: pointer;
  -webkit-appearance: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.flex {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}

.--just-space {
  justify-content: space-between;
}

.--just-center {
  justify-content: center;
}

.--just-end {
  justify-content: flex-end;
}

.--align-str {
  align-items: stretch;
}

.--align-center {
  align-items: center;
}

.--align-end {
  align-items: flex-end;
}

.container {
  max-width: 1170px;
  padding: 0 15px;
  margin: 0 auto;
}

.dn {
  display: none;
}

.banner {
  min-height: 440px;
  padding-top: 123px;
  padding-bottom: 138px;
  background-image: url(../../img/banner.jpg);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}

.banner__wrapper {
  max-width: 505px;
  margin-right: 39px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  min-width: 0;
}

.banner__content {
  max-width: 427px;
  height: 179px;
  background-color: #fff;
}

button {
  padding: 0;
  margin: 0;
  border: 0;
  background-color: transparent;
  border-radius: 0;
  cursor: pointer;
  -webkit-appearance: none;
  transform-origin: center center;
  transition: all ease-out 240ms;
  background-size: contain;
  background-position: center center;
  background-repeat: no-repeat;
  display: block;
}

button:hover {
  transform: scale(1.05);
}

button:focus {
  transform: scale(1.05) trahslateY(-5%);
}

button:active {
  transform: scale(.95);
}

.btn {
  padding: 0 31px;
  font-size: 13px;
  line-height: 42px;
  font-weight: 400;
  color: #fff;
  text-transform: uppercase;
  background-color: var(--accent-color);
}

.submenu__list {
  display: none;
  min-width: 295px;
  position: absolute;
  left: 0;
  top: 56px;
  border: 1px solid #d6d6d6;
  z-index: 1;
}

.submenu__item {
  position: relative;
  padding-top: 17px;
  padding-left: 22px;
  padding-bottom: 15px;
  background-color: #fff;
  transition: all 0.3s;
}

.submenu__item:hover {
  background-color: #d6d6d6;
}

.submenu__item:not(:first-child)::before {
  content: '';
  position: absolute;
  top: 0;
  left: 11px;
  width: 271px;
  height: 1px;
  background-color: #d6d6d6;
}

.submenu__link {
  display: inline-block;
  font-size: 15px;
  font-weight: 300;
  color: var(--text);
  text-transform: capitalize;
}

.header__top {
  height: 56px;
  background-color: var(--text);
}

.header__top-inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.menu__list {
  display: flex;
  align-items: center;
}

.menu__item {
  position: relative;
  padding: 20px;
  transition: all 0.3s;
}

.menu__item::before {
  content: '';
  position: absolute;
  display: block;
  width: 1px;
  height: 35px;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  background-color: rgba(255,255,255,0.1);
}

.menu__item:first-child::before {
  display: none;
}

.menu__link {
  font-size: 15px;
  font-weight: 300;
  color: #ffffff;
}

.menu__item:hover {
  background-color: rgba(0,0,0,0.35);
}

.menu__item:hover .submenu__list {
  display: block;
}

.header__top-link {
  padding: 13px 29px;
  font-size: 12px;
  color: var(--text);
  text-transform: uppercase;
  background-color: #e8d8b6;
}

.header__bottom-inner {
  padding-top: 12px;
  padding-bottom: 14px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-info {
  margin-left: 43px;
  margin-right: auto;
}

.header-info__link {
  padding-left: 49px;
  position: relative;
  font-size: 15px;
  font-weight: 300;
}

.header-info__link::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 37px;
  height: 37px;
  border: 1px solid #323232;
  background-position: center center;
  background-repeat: no-repeat;
}

.header-info__link--events {
  margin-right: 28px;
}

.header-info__link--events::before {
  background-image: url("../../img/icons/events-icon.png");
}

.header-info__link--contacts::before {
  background-image: url("../../img/icons/contacts-icon.png");
}